import { useCallback } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { useRequest } from 'ahooks';

import { confirmCode } from 'api/requests';

import { QUERY, RESULTS } from 'constants/const';
import ROUTES from 'constants/routes';

import { useOnMount } from 'hooks';

let timeoutFN;

export const useConfirm = () => {
  const [query] = useSearchParams();
  const navigate = useNavigate();

  const code = query.get(QUERY.CODE);

  useOnMount(() => {
    timeoutFN = setTimeout(() => {
      navigate(`${ROUTES.RESULT}/${RESULTS.LINK_EXPIRED}`);
    }, 70000);

    return () => clearTimeout(timeoutFN);
  });

  const { run: submit } = useRequest(confirmCode, {
    manual: true,
    onSuccess: () => {
      navigate(`${ROUTES.RESULT}/${RESULTS.SUCCESS}`);
    },
    onError: ({ response }) => {
      if (response.status === 409) {
        navigate(`${ROUTES.RESULT}/${RESULTS.LINK_EXPIRED}`);
        return;
      }

      navigate(`${ROUTES.ERROR}`);
    },
  });

  const handleClick = useCallback(() => {
    submit({ code });
  }, []);

  return { onClick: handleClick };
};
