import { Button, Text, Title } from 'ui';

import CONTENT from 'constants/content';

import { FORM_VALIDATION, MSISDN_KEY } from 'constants/form';
import { composePhoneValidator, composeRequiredValidator } from 'utils/validators';

import { FinalForm } from 'forms/FinalForm';
import { FFPhoneInput } from 'forms';
const { INITIAL_PAGE } = CONTENT;

const validators = () => ({
  [MSISDN_KEY]: [composeRequiredValidator(FORM_VALIDATION.required), composePhoneValidator(FORM_VALIDATION[MSISDN_KEY])],
});

export const InitialPage = () => {
  const handleSubmit = (values) => {
    console.log(values);
  };

  return (
    <>
      <Title>
        {INITIAL_PAGE.TITLE}
      </Title>

      {INITIAL_PAGE.DESCRIPTION && (
        <Text className="mart-xs">
          {INITIAL_PAGE.DESCRIPTION}
        </Text>
      )}

      <FinalForm
        className="mart-se"
        values={{}}
        onSubmit={handleSubmit}
        getValidators={validators}
      >
        {({ invalid, submitting }) => (
          <>
            <FFPhoneInput {...INITIAL_PAGE.FORM[MSISDN_KEY]} />

            <Button type="submit" className="mart-s" disabled={invalid || submitting}>
              {INITIAL_PAGE.SUBMIT_BUTTON_TEXT}
            </Button>

            {INITIAL_PAGE.LEGAL && (
              <Text className="mart-s" isSecondary size="s">
                {INITIAL_PAGE.LEGAL}
              </Text>
            )}
          </>
        )}
      </FinalForm>
    </>
  );
};
