import { MaskedInput } from '../../../MaskedInput';
import { createMask } from '../../helpers';

export const DigitInput = ({ className, maxLength, ...restProps }) => (
  <MaskedInput
    className={className}
    pattern="[0-9]*"
    maxLength={maxLength}
    mask={createMask(maxLength)}
    inputMode="tel"
    unmaskValue={(v) => v.replace(/\D/g, '')}
    {...restProps}
  />
);
